.my_navbar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #14181d;
  height: 4rem;
  width: 100%;

  color: white;
  /* padding: 0 20%; */
  font-size: 2%;
  font-family: 'Minecraftchmc', sans-serif;
}

.my_navbar .bearx_logo {
  margin-right: 2rem;
}
.my_navbar .nav_btn {
  color: white;
  margin-right: 2rem;
  font-size: 2rem;
}
.my_navbar .nav_connect_btn {
  border: #fd7e14 solid 2px;
  border-radius: 0 20px 0 20px;
  font-size: 1.5rem;
  padding: 0 1rem;
  background-color: transparent;
  color: #ffffff;
}

.nav_connect_btn_connected {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.my_navbar_container {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  /* max-width: 1366px; */
  width: 100%;
  margin: auto;
}

.my_navbar_container .my_navbar_social {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #fd7e14;
  width: 100%;
}

.my_navbar_container .my_navbar_social .my_navbar_discord {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
