  font-size: 2rem;
  margin-right: 2rem;
}

.my_navbar_container .my_navbar_social .my_navbar_medium {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: black;
  font-size: 2rem;
  margin-right: 2rem;
}
.font_color_white {
  color: white;
  margin-left: 1rem;
  font-size: 1rem;
}

.menu-bar {
  display: flex;
}

.responsive-menu-bar {
  display: none;
  position: fixed;
  top: 0;
  right: -50%;
  flex-direction: column;
  background-color: #222;
  width: 50%;
  height: 100%;
  justify-content: center;
  align-items: flex-end;
  transition: 0.5s;
}

.responsive-connect {
  background-color: transparent !important;
  border: none !important;
  font-size: 2rem !important;
  color: #ffffff;
}

.responsive-bars {
  display: none;
}

.btn-responsive {
  font-size: 2rem;
  background-color: transparent;
  border: none;
  color: #ffffff;
}

.closeResonsive {
  display: flex;
}

.responsive-logo {
  margin-bottom: 10%;
}

@media screen and (max-width: 768px) {
  .my_navbar {
    justify-content: space-between;
    padding-left: 5%;
    padding-right: 5%;
  }

  .responsive-bars {
    display: flex;
  }

  .menu-bar {
    display: none;
  }
  .responsive-menu-bar {
    display: flex;
  }
}

